.App {
  text-align: center;
  position: relative;
  overflow: hidden;
  background-color: rgb(255, 255, 255);

}

.title {
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;
  font-family: 'Core Circus', sans-serif;
  font-weight: 800;
  letter-spacing: 0;
  color: transparent;
  background-image: url(https://media2.giphy.com/media/v1.Y2lkPTc5MGI3NjExaTJrNnFyOHZrbGNnM2w5dHUxeGFtZmRraXlnYjBhbWNjbjB5dmluMiZlcD12MV9pbnRlcm5hbF9naWZfYnlfaWQmY3Q9Zw/iYTXFJofI7I987H92k/giphy.gif);
  background-size: cover;
  background-color: transparent;
  -moz-background-clip: text;
  -webkit-background-clip: text;
  font-size: 100px;
  -webkit-text-stroke: 1px #000;
  -webkit-text-stroke-width: 1px;
  width: 100%;
}


@media (max-width: 480px) {
  .title {
    font-size: 60px;
  }
}

.seo-introduction,
.seo-regalos,
.seo-fiscalidad,
.seo-redes,
.seo-contacto {
  color: #333;
  margin-bottom: 20px;
  position: absolute;
  left: 0;
  right: 0;
  padding: 3%;
  font-family: 'Core Circus', sans-serif;
  font-weight: 600;
  font-size: 1.2em;
  text-align: justify;
  z-index: 6;
  overflow: auto;
  box-sizing: border-box;
}

.seo-introduction {
  top: 0;
  bottom: 0;
}

.seo-regalos {
  top: 450px;
}

.seo-fiscalidad {
  top: 1050px;
}

.seo-redes {
  top: 1900px;
}

.seo-contacto {
  top: 2250px;
}

@media (max-width: 480px) {
  .seo-introduction,
  .seo-regalos,
  .seo-fiscalidad,
  .seo-redes,
  .seo-contacto {
    font-size: 20px; 
    font-weight: 500;
    text-align: left; 
  }

  .seo-regalos {
    top: 550px; 
  }

  .seo-fiscalidad {
    top: 1025px; 
  }

  .seo-redes {
    top: 1600px; 
  }

  .seo-contacto {
    top: 2150px; 
  }
}

.container1 {
  position: relative;
  font-family: sans-serif;
  margin-left: auto;
  margin-right: auto;
  width: 95%;
  z-index: 1;
  margin-bottom: 50px;
}

.container1::before,
.container1::after {
  content: "";
  background-color: #ea8b19;
  position: absolute;
}

.container1 .box {
  width: 100%;
  height: 2800px;
  background-color: rgba(255, 255, 255, 0.075);
  border: 2px solid rgba(255, 255, 255, 0.222);
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  border-radius: 0.7rem;
  transition: all ease 0.3s;
  z-index: 2;
  box-shadow: 0px 0px 5px 5px #ea8b19;
}

@media (max-width: 480px) {
  .container1 .box {
    height: 3125px;
  }
}

/* CSS of this is wordEffect */
h2 {
  font-family: 'Core Circus', sans-serif;
  font-size: 130px;
  text-align: center;
  position: absolute;
  color: rgba(45, 45, 45, 1);
  letter-spacing: 0rem;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 2em;
  z-index: 3;

  @media (max-width: 480px) {
    font-size: 60px;
  }
}

h2:before {
  content: "!";
  position: absolute;
  overflow: hidden;
  color: transparent;
  width: 100%;
  z-index: 4;
  text-shadow: none;
  left: 0%;
  text-align: right;

  @media (max-width: 480px) {
    left: 0%;
    text-align: right;
    top:1000px
  }
}




