
  
  .wordEffectContainer {
    position: absolute;
    top: 120px;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgba(45, 45, 45, 1);
    font-size: 130px;
    text-align: center;
    line-height: 1;
    margin: 0;
    letter-spacing: 1rem;
    
    
  }
  
  @media (max-width: 480px) {
    .wordEffectContainer {
      margin-top: -775px;/* Adjust the margin-top value as needed */
      font-size: 90px;
      text-align: center;
    }
  }
  
  h2 {
    font-family: 'Core Circus', sans-serif;
    font-size: 130px;
    text-align: center;
    position: absolute;
    color: rgba(45, 45, 45, 1);
    letter-spacing: 0rem;
    left: 50%;
    transform: translate(-50%, -50%);
    margin-top: 2em;
    z-index: 3;
  }
  
  h2:before {
    content: '!';
    position: absolute;
    overflow: hidden;
    color:transparent;
    width: 100%;
    z-index: 4;
    text-shadow: none;
    left: 0%;  
    text-align: right;
  }
  
  @media (max-width: 480px) {
    h2 {
      font-size: 75px;
      transform: translate(-50%, -50%);
      left: 50%; 
      top: 50%; 
    }
  
    h2:before {
      left: 0%;
      text-align: right;
      margin-top: -800x;/* Adjust the margin-top value as needed */
    }
  }
  
  
  