$facebook: #00000000;
$twitter: #00000000;
$youtube: #00000000;
$googlePlus: #00000000;

body {
  background-color: #DEB841;
}

.container {
  position: absolute;
  top: 950px; /*mover en la web los 4*/
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 7;
}

@media (max-width: 480px) {
  .container {
    top: 600px;
  }
}

.menu {

  text-align: center;
  position: relative;
  margin: auto;
  transform-style: preserve-3d;
  padding: 0;
  list-style-type: none;
  text-align: center;
  top: 1600px;
  transform: translateY(-50%);
  

  li {
    transform-style: preserve-3d;
    position: relative;
    margin: 80px;
    display: inline-block;
    transform: rotateX(-20deg) rotateY(45deg);
    animation: move 2s ease infinite;

    @media (max-width: 480px) {
      margin: 10px; 
    }

    a {
      height: 100px;
      width: 140px;
      display: inline-block;
      line-height: 90px;
      color: #fff;
      text-decoration: none;
      position: relative;

      @media (max-width: 480px) {
        height: 105px; 
        width: 90px; 
        line-height: 80px;
        display: block; 
      }

      img.social-logo {
        @media (max-width: 480px) {
          height: 105px;
          width: 105px;
        }
      }

      &::before {
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        left: -100%;
        top: 0;
        bottom: 0;
        margin: auto;
        transform-origin: right;
        transform: rotateY(-90deg);
        transform-style: preserve-3d;
      }

      &::after {
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        transform: rotateX(-90deg);
        transform-origin: top;
      }

      i.fa {
        font-size: 3rem;
        text-shadow: 0px 0px 2px rgba(0, 0, 0, 0.5),
          1px 1px 2px rgba(0, 0, 0, 0.5),
          2px 2px 2px rgba(0, 0, 0, 0.5);
      }
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: -20px;
      margin: auto;
      height: 100%;
      width: 100%;
      display: inline-block;
      background-color: rgba(0, 0, 0, 0.2);
      transform-origin: bottom;
      transform: rotateX(90deg) translateY(0px);
      z-index: -1;
      animation: shadowAnimation 2s ease infinite;
      border-radius: 30px;
    }
  }

  @for $i from 1 through 4 {
    li:nth-child(#{$i}) {
      animation-delay: #{$i * 0.2}s;

      &::after {
        animation-delay: #{$i * 0.2}s;
      }
    }
  }
}
@media (max-width: 480px) {
  .menu {
    display: grid;
    grid-template-columns: repeat(2, 1fr); /* Display elements in a 2x2 grid */
    grid-gap: 20px; /* Adjust the grid gap as needed */
    top: 1550px; /* Adjust the distance from the top */
    margin-left: 65px; /* Adjust the number of pixels from the right */

  }
}

@media (min-width: 481px) {
  .menu {
    display: block; /* Revert to original PC layout */
    top: 1400px; /* Revert the distance from the top */
  }
}

.facebook {
  background-color: $facebook;

  &::before {
    background-color: darken($facebook, 20%);
  }

  &::after {
    background-color: darken($facebook, 30%);
  }
}

.twitter {
  background-color: $twitter;

  &::before {
    background-color: darken($twitter, 20%);
  }

  &::after {
    background-color: darken($twitter, 30%);
  }
}

.googlePlus {
  background-color: $googlePlus;

  &::before {
    background-color: darken($googlePlus, 20%);
  }

  &::after {
    background-color: darken($googlePlus, 30%);
  }
}

.youtube {
  background-color: $youtube;

  &::before {
    background-color: darken($youtube, 20%);
  }

  &::after {
    background-color: darken($youtube, 30%);
  }
}

@keyframes move {
  0%, 100% {
    transform: rotateX(-20deg) rotateY(45deg) translateY(-10px);
  }

  50% {
    transform: rotateX(-20deg) rotateY(45deg) translateY(10px);
  }
}

@keyframes shadowAnimation {
  0%, 100% {
    bottom: -40px;
    opacity: 0.2;
  }

  50% {
    bottom: -10px;
    opacity: 1;
  }
}
