.AnimatedBackground {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  z-index: 0;
}

@media (max-width: 480px) {
  /* Apply different background position on mobile devices */
  .AnimatedBackground {
    background-position: right center;
  }
}


  
